<script>
import { Message } from 'element-ui';


export default {
  data(){
    return{
      activeIndex:"2",
      speedNum:50,
      dialogTableVisible:false,
      category:[],
      data:[],
      mp3url:"",
      anchor_index:-1,
      defaultItem:{},
      isPlay: false,
      playId:0,
      wordsNum:0,
      textarea:"",
      max:1000,
      peiyin:"",
      cg:true,
      sign:""
    }
  },
  created () {
    
  },
  mounted() {
     this.getData();
     this.getCategory();

     this.$store.commit("setActiveIndex",this.activeIndex)
     
  },
  methods:{
    tips(){
      this.tip("敬请期待!")
    },
    tip(text){
      Message.warning({
        type:"warning",
        message:text,
        center:true,
        duration:1500
      })
    },
    //获取分类
    getCategory(){
      this.axios({
        method: "post",
        url: "/api/tts/cat_list",
        data: {},
      }).then((res) => {
        
          this.category=res.data.data.items;
          
        })
        .catch((err) => {
          console.log(err);
        });
    },

    //所有数据
    getData() {
      this.axios({
        method: "post",
        url: "/api/tts/list_new",
        data: {},
      })
        .then((res) => {
          // console.log(res.data.items);
          
          let arr=res.data.data;
          var first = {};
          
          var map = {};
          
          for (var i = 0; i < arr.length; i++) {
            map[arr[i].id] = arr[i].list;

            for(var ai in arr[i].list){

              if(i == 0){
                first = arr[i].list[ai];
              }

              if(arr[i].list[ai].id == this.$route.query.id){
                  console.log("id="+this.$route.query.id)
                  this.defaultItem = arr[i].list[ai];
              }
            }

          }        
          
          this.data=map;
          
          
          if(Object.keys(this.defaultItem).length === 0){
            this.defaultItem = first;
          }
          
        })
        .catch((err) => {
          console.log(err);
        });
    },
    anchor_hover(i,item){
      this.anchor_index=i;
      this.defaultItem = item;
      this.dialogTableVisible = false;
      this.cg = true;
    },
    play(item,e){

      if(this.isPlay){
        let plid = this.playId;
        this.stop();
        if(plid == item.id){
          console.log("stop")
          return;
        }
      }

      console.log("play "+item.id);
      
      this.mp3url = item.demoUrl;
      this.playId = item.id;
      console.log(this.mp3url)
      
      if(this.$refs.audio.readyState != 4){
        this.$refs.audio.oncanplay =()=>{
            this.playAdu();
        }
      }else{
        this.playAdu();
      }
      
    },
    stop(){
      this.$refs.audio.pause();
      this.isPlay = false;
      this.playId = 0;
    },
    pause(){
      this.isPlay = false;
      this.playId = 0;
    },
    playAdu(){
      this.isPlay = true;
      this.$refs.audio.play();
    },
    make(play,callback){
      if(this.textarea.length <= 0){
        this.$alert('请输入配音内容', '操作失败', {
          confirmButtonText: '确定'
          
        });
        return;
      }

      if(this.cg == false && this.peiyin.length > 0){
        this.$refs.py.play();
        return;
      }

      const loading = this.$loading({
          lock: true,
          text: '努力生成中，请稍后...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });

      this.cg = false;

      this.axios({
        method: "post",
        url: "/api/tts/make",
        data: {
          "text":this.textarea,
          "anchor":this.defaultItem.id,
          "speed":this.speedNum
        },
      }).then((res) => {
        loading.close();

        if(res.data.code == 200){
          this.peiyin = res.data.data.content
          this.sign = res.data.data.sign
          this.$refs.py.src = "data:audio/mp3;base64,"+this.peiyin

          if(!play){
            callback()
            return
          }

          if(this.$refs.py.readyState != 4){
            this.$refs.py.oncanplay =()=>{
                this.$refs.py.play()
            }
          }else{
            this.$refs.py.play()
          }

        }else{
          this.tip(res.data.msg)
        }

      }).catch((err) => {
        loading.close();
        this.tip(err)
        console.log(err);
      });

    },
    exportAudio(){
      this.tip("开通会员享更多无限制功能，限时9.9元每月")

      var that = this

      const callback = function(){
        that.axios.get("/api/tts/export", {params: {sign:that.sign}, responseType: 'blob'})
        .then((res) => {        
           that.exportFile([res.data],that.sign+'.mp3',true)
        })
        .catch((err) => {})
      }

      
      this.make(false,callback)       
      

    },
    exportText(){
      this.tip("开通会员享更多无限制功能，限时9.9元每月")

      if(this.sign.length <=0){
        this.tip("请先生成配音")
        return
      }

      this.axios.get("/api/tts/export_vtt", {params: {sign:this.sign}, responseType: 'blob'})
      .then((res) => {        
         this.exportFile([res.data],this.sign+'.vtt',false)
      })
      .catch((err) => {})
    },
    exportFile(data,file,mp3){
      if(mp3){
        var ext = {type: "audio/mpeg"};
      }else{
        var ext = {};
      }

       const blob = new Blob(data,ext); 
       
       const elink = document.createElement('a');
       elink.download = file;
       elink.style.display = 'none';
       elink.href = URL.createObjectURL(blob);
       document.body.appendChild(elink);
       elink.click();
       URL.revokeObjectURL(elink.href); 
    },
    changeinput(val){
      
      this.cg = true
    }
  }
}
</script>